/** 
 *  
 * @param date  
 * @param format 
 *      
 *     y:, 
 *     M:(1-12), 
 *     d:(1-31), 
 *     h:(0-23), 
 *     m:(0-59), 
 *     s:(0-59), 
 *     S:(0-999),
 *     q:(1-4)
 *     W:
 * @return String
 * @author yanis.wang@gmail.com
 */
import Vue from 'vue' 
import {dateFormat} from '../utils/date'
Vue.filter('dateFormat', dateFormat);





